<template>
    <div class="login">
        <div class="login-form">
            <div class="login-header">
            <p>登陆</p>
        </div>
        <el-input
                placeholder="请输入用户名"
                suffix-icon="fa fa-user"
                v-model="userName"
                style="margin-bottom: 18px"
        >
        </el-input>

        <el-input
                placeholder="请输入密码"
                suffix-icon="fa fa-keyboard-o"
                v-model="passWord"
                type="password"
                style="margin-bottom: 18px"
                @keyup.native.enter="login"
        >
        </el-input>

        <el-button
                type="primary" :loading="loginLoading"
                style="width: 100%;margin-bottom: 18px"
                @click.native="login"
        >
          登录
      </el-button>

    </div>
  </div>
</template>

<script>
    
    export default {
        data() {
            return {
                userName: 'user',
                passWord: '123456',
                loginLoading: false
            }
        },
        methods: {
            login() {
                const {userName,passWord} = this;
                if(userName == ''){
                    this.$notify.error({title:'失败',message:'请输入账户名'});
                    return false;
                }
                if(passWord == ''){
                    this.$notify.error({title:'失败',message:'请输入密码'});
                    return false;
                }

               this.$post('login/userLogin',{userName,passWord}).then(res=>{
                    if(res.statusCode == "SUCCESS"){
                        let user = res.data;
                        this.$cookies.set('user_id',user.userinfo.id,3600);
                        // this.getPowerALL(user.userinfo.id);
                        this.$cookies.set('nickname',user.userinfo.nikeName,3600);
                        this.$cookies.set('token',user.token,3600);
                        this.$notify({type:'success',title:'成功',message:'登录成功'});
                        this.loginLoading = false;
                        this.$router.push({path: '/'});
                    }
               })
            },
            getPowerALL(id){
                this.$post('userInfo/getUserInfoById',{id}).then(res=>{
                  if(res.statusCode == "SUCCESS"){
                      let power =[];
                      res.data.roleList.forEach((item1,index1)=>{
                          item1.userRole.userRoleToPowers.forEach((item2,index2)=>{
                            power.push({
                                url:item2.rolePower.url,
                                name:item2.rolePower.name
                            })
                          })
                      })
                      console.log(power);
                  }
              })
            }
        }
    }
</script>

<style lang="less" scoped>
  @import "Login.less";
</style>
